.app__header {
    background-color: var(--color-green);

}

.app__header-h1 {
    padding-top: 0rem;
    font-family: var(--font-base);
    color: var(--color-golden);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 70px;
    font-size: 60px;
}

.app__wrapper_info p {
    line-height: 30px;
}
    

.app__header-img img {
    margin-top: 0rem;
    width: 80%;
}

.custom__button {
    margin-top: 1rem;
    box-sizing: 10px;
    border: 5px solid rgba(0, 131, 116, 0.7);
}

@media screen and (min-width: 2000px) {
    .app__header-h1 {
        font-size: 180px;
        line-height: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__header-h1 {
        font-size: 90px;
    }
}

@media screen and (max-width: 650px) {
    .app__header-h1 {
        font-size: 70px;
        line-height: 87px;
    }
}

@media screen and (max-width: 450px) {
    .app__header-h1 {
        font-size: 50px;
        line-height: 70px;
    }
}