.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--color-green);
    margin-top: -1rem;
    padding: 0.5rem 2rem;
}

.app__navbar-logo {
    padding-top: 1.5rem;
    margin-bottom: 0.001rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo-bigg {
    width: 180px;
    margin-bottom: 0.01rem;
    color: var(--color-white);
    padding-left: 4rem;
    padding-bottom: 0px;
    font-size: 170%;
    font-family: var(--font-base);
    
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;
}

.app__navbar-links li {
    margin: 0 1rem;
    cursor: pointer;
}

.app__navbar-links li:hover {
    color: var(--color-grey);
}

.app__navbar-login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
/* .logout-button {
    display: flex;
    justify-content: flex-end;
    align-items: center;
} */

.app__navbar-login a {
    margin: 0 1rem;
    text-decoration: none;
    transition: .5s ease;
}
.logout-button button {
    background: #fff;
    color: var(--primary);
    border: 2px solid var(--primary);
    padding: 6px 10px;
    border-radius: 4px;
    font-family: "Poppins";
    cursor: pointer;
    font-size: 1em;
}

.app__navbar-login a:hover {
    border-bottom: 1px solid var(--color-golden);
}

.logout-button button:hover {
    border-bottom: 3px solid var(--color-golden);
}
.app__navbar-login div {
    width: 1px;
    height: 30px;
    background: var(--color-grey);
}
/* .logout-button div {
    width: 1rem;
    height: 2rem;
    background: var(--color-grey);
} */

.app__navbar-smallscreen {
    display: none;
}

.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--color-green);
    transition: .5s ease;

    flex-direction: column;
    z-index: 5;
}

.app__navbar-smallscreen_overlay .overlay__close {
    font-size: 27px;
    color: var(--color-golden);
    cursor: pointer;

    position: absolute;
    top: 20px;
    right: 20px;
}

.app__navbar-smallscreen_links {
    list-style: none;
}

.app__navbar-smallscreen_links li {
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
}

.app__navbar-smallscreen_links li:hover {
    color: var(--color-white);
}

@media screen and (min-width: 2000px) {
    .app__navbar-logo img {
        width: 210px;
    }
}

@media screen and (max-width: 1150px) {
    .app__navbar-links {
        display: none;
    }

    .app__navbar-smallscreen {
        display: flex;
    }
}

@media screen and (max-width: 650px) {
    .app__navbar {
        padding: 1rem;
    }

    .app__navbar-login {
        display: none;
    }

    .app__navbar-logo img {
        width: 110px;
    }
}